@import url(https://fonts.googleapis.com/css2?family=Covered+By+Your+Grace&family=Roboto:wght@400;500;700&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Parisienne&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Covered+By+Your+Grace&family=Roboto:wght@400;500;700&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

/* .App {
  text-align: center;
} */

* {
  /* font-family: 'Covered By Your Grace'; */
  font-family: 'Roboto';
}

.Filters {
  width: 275px;
  flex-direction: row;
}

.form-div {
  width: 550px;
  margin: auto;
  display: flex;
}

.countryTitle {
  font-family: 'Covered By Your Grace' !important;
  font-size: 24px;
  font-weight: 500;
  color: #1e1b5e;
  text-align: center;
  margin: auto;
}

@media screen and (max-width: 650px) {
  .Filters {
    width: 150px;
    font-size: 12px;
  }
  .form-div {
    width: 300px;
    font-size: 12px;
  }
}

.Homepage {
  display: flex;
  flex-direction: column;
  justify-content: center;
  padding: 20px 0px 0px 0px;
  text-align: center;
  background-color: #dfdfde;
}

.main-title {
  display: flex;
  justify-content: center;
  margin: auto;
  font-size: 38px;
  font-weight: bold;
  font-family: 'Covered By Your Grace' !important;
  color: #3b89a0;
}

.welcome-title {
  display: flex;
  justify-content: center;
  flex-wrap: wrap;
  margin: 20px auto;
}

.welcome-text {
  display: flex;
  width: 580px;
  font-size: 20px;
  font-weight: bold;
}

.second-text {
  color: #3b89a0;
}

.photo {
  margin-top: 20px;
  width: 22%;
  border-radius: 45%;
}

.data-photo {
  display: none;
}

@media (max-width: 768px) {
  .welcome-title {
    width: 320px;
  }

  .welcome-text {
    font-size: 18px;
  }

  .photo {
    margin-top: 20px;
    width: 50%;
  }
}

.WebcamList {
  display: flex;
  flex-wrap: wrap;
  flex-direction: column;
  padding: 20px;
}

.explore-title {
  display: flex;
  margin: 20px 0px 0px 15px;
  font-size: 20px;
  font-weight: bold;
  vertical-align: middle;
  line-height: 30px;
}

.text {
  font-family: 'Covered By Your Grace';
  font-weight: 600;
  font-size: 28px;
  margin-left: 10px;
  color: #3b89a0;
}

.total-title {
  display: flex;
  margin-left: 15px;
  font-size: 18px;
  font-weight: bold;
  margin-bottom: 20px;
  line-height: 28px;
}

.list {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-evenly;
}

.card-body {
  margin: 0px;
  padding: 10px;
}

.card {
  width: 160px;
  margin: 10px 5px;
  background-color: white;
  border: solid 1px white;
}

.card-title {
  margin: 0px;
}

.card-body {
  padding: 0px;
}

.button {
  overflow: auto;
  padding: 10px;
  line-height: 12px;
  font-size: 11px;
  font-weight: bold;
  height: 60px;
  width: 155px;
  color: black !important;
  background-color: white;
  border: solid 1px white;
}

.button:hover {
  color: white !important;
  background-color: #3b89a0;
  border: solid 1px #3b89a0;
}

.containerFooter {
  display: flex;
  flex-direction: column;
  justify-content: center;
  color: #4ea2bb;
  background-color: #07052b;
  height: 100px;
  border-top: 5px solid #cecece;
  border-bottom: 5px solid #cecece;
  line-height: 18px;
  font-size: 13px;
  padding: 10px 20px;
}

.containerFooter a:link {
  width: 130px;
  color: #498fa4 !important;
}

.containerFooter a:visited {
  color: #498fa4 !important;
}

.containerFooter a:hover {
  color: white !important;
}

.containerFooter {
  list-style: none;
  display: flex;
  flex-direction: column;
}
.copyRight {
  display: flex;
  justify-content: center;
  margin-top: 10px;
  align-content: center;
}

.footerList {
  margin: 0px auto;
}

.icon {
  width: 18px;
  margin: 0px 3px;
}
.copy {
  margin-left: 10px;
}

@media (max-width: 900px) {
  .containerFooter {
    display: flex;
    align-items: center;
  }
  .footerList {
    width: 250px;
    text-align: center;
  }
}

